* {
  box-sizing: border-box;
}
html,
body {
  height: 100%;

}
body {
  
  overflow: hidden;

  margin: 0;
  background-color: #0e1110;
  // font-family: 'IBM Plex Sans', sans-serif;
  font-family: 'Roboto', 'Arial', sans-serif;
  font-size: .9375rem;
  line-height: 1.5;
  color: #ebebeb;
    cursor: url("../img/cursor.png"), auto !important;
    // background-image: url(../img/background.jpg);
    background-size: cover;
    background-repeat: no-repeat;
}
h2 {
    font-weight: 300;
    font-family: 'IBM Plex Sans',sans-serif;
    font-size: 44px;
    font-size: 2.75rem;
    line-height: 1.2;
    letter-spacing: -.04em;
}
.container {
  max-width: 1900px;
  padding: 0px 20px;
  margin: 0 auto;
}
.main-header {
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  min-height: 90px;
  padding: 0px 40px;
  top: 0;
  left: 0;
}
.main-header__hamb {
  width: 52px;
}
.main-header__hamb-line {
  display: block;
  // width: 52px;
  height: 1px;
  background-color: #ffffff;
}
.main-header__hamb-line:first-child {
  margin-bottom: 6px;
}
// .main-content {
//   padding-top: 90px;
// }
.main-header__logo {
  font-size: 18px;
  line-height: 29px;
  font-family: 'IBM Plex Sans',sans-serif;
  // font-weight: 500;
  // display: block;
  text-transform: uppercase;
  -webkit-text-stroke-width: 0.3px;
  -webkit-text-stroke-color: #ebebeb;
  -webkit-text-fill-color: transparent;
}
.main-header__top-menu {
  font-size: 13px;
  margin: 0;
  padding: 0;
  list-style: none;
}
.top-menu__link {
  font-size: 14px;
  font-size: 0.9em;
  line-height: 19px;
  margin-left: 42vw;
  font-family: 'Roboto', 'Arial', sans-serif;
  color: inherit;
  text-decoration: none;
}
.top-menu__link::selection {
    background: #fbff00;
}
.im__content {
  position: fixed;
  top: 25.5vh;
  display: flex;
  width: 265px;
  flex-direction: column;
 left: 45px;
  justify-content: flex-start;
  margin-bottom: auto;
  // align-items: flex-start;
}
.im__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 13px;
  line-height: 23px;
}
.im__subname {
  margin: 0;
  font-size: 17px;
  color: #ababab;
  font-family: 'IBM Plex Sans', sans-serif;
  font-weight: 300;
  margin-bottom: 17px;
}
.main-screen__im {
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  padding-top: 240px;
  padding-bottom: 279px;
}
.main-screen {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100vh;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-right: 60px;
}
.main-nav__link {
  font-size: 5.5rem;
  line-height: 8rem;
  font-family: 'IBM Plex Sans',sans-serif;
  text-decoration: none;
  // font-weight: 500;
  // display: block;
  text-transform: uppercase;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #ebebeb;
  -webkit-text-fill-color: transparent;
}
.main-nav__link:hover {
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: transparent;
  -webkit-text-fill-color: #ebebeb;
}
.main-screen__menu {
  mix-blend-mode: difference;
  position: fixed;
  right: 5vw;
  top: 50%;
  transform: translateY(-50%);
  z-index: 20;
}

.main-nav__link::selection {
  background: #fbff00;
  color: white;
}
.main-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  width: 100vw;
  height: 60px;
  // border-top: 1px solid #2b2b2b;
}
.main-screen__contacts {
  position: fixed;
  display: flex;
  justify-content: flex-start;
  bottom: 25.2vh;
  left: 45px;
}
.main-screen__contacts-link-wrapper {
  position: relative;
  margin-right: 87px;
}
.main-screen__contacts-link-wrapper:last-child::after {
  display: none;
}
.main-screen__contacts-link-wrapper::after {
  position: absolute;
  content: "";
  top: 50%;
  margin-top: 0.5px;
  margin-left: 20px;
  width: 45px;
  height: 1px;
  background-color: #2b2b2b;
}
.im__name {
  margin: 0;
  // font-family: 'Caveat', cursive;
  font-size: 28px;
  line-height: 27px;
  margin-bottom: 5px;
  color: #ebebeb;
  text-decoration: none;
  font-weight: 300;
}
.im__list-item > p {
  margin: 0;
}
.im__list-item span {
  color: #757575;
}
.main-screen__contacts-link {
  font-size: 13px;
  line-height: 19px;
  font-family: 'Roboto', 'Arial', sans-serif;
  color: inherit;
  text-decoration: none;
}
.ticker__text {
  font-size: 22px;
  line-height: 28px;
  margin: 0;
  margin-bottom: 3px;
  font-family: 'IBM Plex Sans',sans-serif;
  // font-style: italic;
  text-decoration: none;
  text-transform: uppercase;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #2b2b2b;
  -webkit-text-fill-color: transparent;
}
.ticker__text span {
  font-size: 22px;
  line-height: 28px;
  margin: 0;
  margin-bottom: 3px;
  font-family: 'IBM Plex Sans',sans-serif;
  // font-style: italic;
  text-decoration: none;
  text-transform: uppercase;
  -webkit-text-stroke-width: 0px;
  -webkit-text-stroke-color: #ffffff;
  -webkit-text-fill-color: #ffffff;
  opacity: 0.25;
  margin-right: 0;
}
.fly-block {
  position:absolute;
  z-index: 4;
  // width:500px;
  // height:800px;
  // background-color: white;
  margin:-5% 0 0 -7%;
  transition: top 0.5s, left 0.5s;
  -webkit-transition: top 0.5s, left 0.5s;
}
.fly-block__img {
  display: none;
  max-width: 353px;
}
.fly--current {
  display: block;
}
.ticker__text {
  width: 100%;
}
.ticker-left,
.ticker-right {
  // width: 100%; /* the plugin works for responsive layouts so width is not necessary */
  overflow: hidden;
}

//Курсор
.cursor-img {
  position: absolute;
  width: 40px;
  height: 40px;
  transition-timing-function: ease-out;
  z-index: -2;
}
.cursor-img--1 {
  opacity: 1;
  background: url("/img/hi1.png") no-repeat  50% 50%;
  background-size: cover;
  transition-duration: 1600ms;
}
.cursor-img--2 {
  opacity: 1;
  background: url("/img/hi2.png") no-repeat  50% 50%;
  background-size: cover;
  transition-duration: 2000ms;
}
.cursor-dn {
  opacity: 0;
}
.page-bg-wrapper {
  width: 100%;
  height: 100%;
  position: fixed;
}
.page-bg {
  width: 110%;
  height: 110%;
  left: -5%;
  top: -5%;
  position: absolute;
  background-image: url(../img/background.jpg);
  background-size: cover;
  background-position: center;
}
